.add_transaction_container {
  width: 100vw;
  display: flex;
  place-content: center;
}

.add_transaction_mid_container {
  width: 60%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.add_transaction_card {
  background-color: white;
  width: 420px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  margin-top: 80px;
  margin-bottom: 20px;
  border-radius: 15px;
  box-shadow: 1px 1px black;
}

#type {
  background-color: white;
  height: 26px;
}

.add_transaction_items {
  width: 100%;
  padding: 10px 20px;
  margin: 4px;
  display: flex;
  flex-direction: column;
}

.add_transaction_items input,
#type {
  margin: 6px 20px;
  width: 60%;
  align-self: center;
}
#desc {
  height: 60px;
  text-align: center;
  resize: none;
  width: 60%;
  align-self: center;
}

#amount {
  height: 26px;
}

.add_transaction_btn {
  background-color: #323e41;
  padding: 6px 20px;
  border-radius: 10px;
  color: white;
  font-weight: bold;
  margin-bottom: 10px;
}
.add_transaction_heading {
  width: 100%;
  margin-top: 20px;
  text-transform: uppercase;
  font-size: 24px;
  text-align: center;
  margin-bottom: 10px;
  color: black;
  text-shadow: 1px 1px grey;
}
@media (max-width: 400px) {
  .add_transaction_mid_container {
    width: 100%;
  }
  .add_transaction_card {
    width: 80%;
  }
  .add_transaction_items input,
  #type,
  #desc {
    width: 80%;
  }
}

.home__container {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  align-content: right;
  justify-content: center;
  margin-top: 60px;
}

.home__box1,
.home__box2,
.home__box3,
.home__box4 {
  background-color: white;
  width: 44%;
  height: 300px;
  margin: 20px 20px;
  /* box-shadow: 1px 1px black; */
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 13px 10px -7px rgba(0, 0, 0, 0.1);
}

.home__box1:hover,
.home__box2:hover,
.home__box3:hover,
.home__box4:hover {
  transform: scale(1.05, 1.05);
}
@media (max-width: 700px) {
  .home__box1,
  .home__box2,
  .home__box3,
  .home__box4 {
    width: 80%;
    height: 260px;
  }
}
@media (max-width: 500px) {
  .home__box1,
  .home__box2,
  .home__box3,
  .home__box4 {
    width: 80%;
    height: 200px;
  }
}

.transaction__container {
  margin-top: 60px;
  width: 100%;
  height: 600px;
  display: flex;
  justify-content: center;
}
.transaction_heading {
  color: white;
  width: 100%;
  text-align: center;
  text-transform: uppercase;
  margin-top: 20px;
  font-size: 26px;
}
.transaction_list_container {
  width: 80%;
  height: 600px;
  /* background-color: red; */
  display: flex;
  flex-direction: column;
}
.transaction_list_item:hover {
  transform: translate3D(0, -1px, 0) scale(1.03);
}
.transaction_list_item {
  margin-top: 40px;
  border-radius: 10px;
  box-shadow: 1px 1px black;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  background-color: white;
  padding: 10px 20px;
}
.transactin_list_item_row1 {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  position: relative;
  /* background-color: rebeccapurple; */
}
.transaction_description {
  display: flex;
  margin: 10px 60px;
}

.transaction_item_delete_btn {
  background-color: red;
  padding: 10px 20px;
  color: white;
  border-radius: 6px;
  border: none;
  font-weight: bold;
  letter-spacing: 0.6px;
  cursor: pointer;
}

.transaction_amount {
  color: green;
  font-size: 26px;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}

.transaction_item_description_heading {
  margin-top: 10px;
  text-decoration: underline;
  font-weight: bold;
  padding-left: 40px;
  font-size: 24px;
}

.transaction_item_description_btn:hover {
  cursor: pointer;
}
.transaction_item_description_btn {
  position: absolute;
  bottom: -30px;
  /* background-color: white; */
}

.transaction_item_description_unactive {
  display: none;
}
.transaction_item_description_active {
  display: flex;
  flex-wrap: wrap;
  margin-top: 20px;
  padding-top: 10px;
}

@media (max-width: 600px) {
  .transaction_list_container {
    width: 100%;
  }
}

@media (max-width: 450px) {
  .transaction_list_container {
    width: 80%;
  }
  .transaction_list_item {
    padding: 5px 10px;
  }
  .transaction_description {
    margin: 10px 20px;
  }
}

.user__container {
  width: 100%;
  display: flex;
  justify-content: center;
}
.user_main_container {
  width: 90%;
  background-color: white;
  display: flex;
  margin-top: 80px;
  border-radius: 10px;
}
.user_profile_container {
  border-top-left-radius: 10px;
  width: 36%;
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 2px solid #00000033;
}
.user_profile_image_card {
  position: relative;
}
.user_image_camera_icon {
  position: absolute;
  bottom: 14px;
  right: 14px;
  background-color: white;
  border-radius: 50%;
  padding: 4px 8px;
}
.user_details_container {
  width: 60%;
  padding: 0 2%;
  height: 100vh;
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: start;
}

.user_profile_image {
  border-radius: 50%;
  width: 200px;
  height: 200px;
  justify-self: center;
  margin-top: 10px;
  object-fit: cover;
}
.user_full_name {
  color: black;
  font-size: 26px;
  margin-top: 8px;
}
.user_username {
  color: #91969c;
  margin-top: 2px;
}
.user_familyId {
  color: black;
  margin-top: 20px;
}

.user_transaction_details {
  display: flex;
  width: 100%;
}

.user_transaction_number {
  width: 50%;
  text-align: center;
  padding-top: 100px;
}
.user_transaction_number span,
.user_transaction_total_amount span {
  font-size: 30px;
  color: red;
}
.user_transaction_number div,
.user_transaction_total_amount div {
  margin-bottom: 10px;
}
.user_transaction_total_amount {
  width: 50%;
  text-align: center;
  padding-top: 100px;
}
.user_family_member_heading {
  margin-left: 10px;
  font-size: 24px;
}
.user_family_members {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-top: 40px;
  margin-left: 40px;
  margin-right: 40px;
}
.user_family_member_cards {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  justify-content: center;
  align-items: center;
}
.user_family_member_image {
  border-radius: 50%;
  width: 80px;
  height: 80px;
  margin: 0px 20px;
  margin-top: 10px;
  margin-bottom: 6px;
  object-fit: cover;
}
.user_family_member_card {
  margin: 10px 20px;
}
.user_family_member_username {
  font-size: 14px;
}

@media (max-width: 650px) {
  .user_main_container {
    flex-direction: column;
  }
  .user_profile_container {
    width: 100%;
    justify-content: center;
    align-items: center;
    padding-bottom: 20px;
  }
  .user_details_container {
    margin-top: 1px;
    width: 100%;
    height: 100%;
  }
  .user_transaction_total_amount,
  .user_transaction_number {
    padding-top: 40px;
  }
}

.navbar__logo {
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  color: black;
  text-transform: uppercase;
}

.navbar {
  width: 100vw;
  height: 60px;
  background-color: #ffac41;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  position: fixed;
  top: 0;
  z-index: 10;
}
.navbar__heading {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  color: black;
  padding-right: 20px;
  font-weight: 400;
}

.navbar__btn {
  margin-left: 10px;
}

.navbar__menu {
  position: absolute;
  top: 0px;
  left: -200px;
  display: flex;
  flex-direction: column;
  background-color: #ffac41;
  /* background-color: crimson; */
  width: 200px;
  height: 100vh;
  transition: 850ms;
}

.navbar__active {
  left: 0;
  z-index: 1;
}

.cancel__btn {
  display: flex;
  width: 100%;
  justify-content: flex-end;
  padding-right: 20px;
  padding-top: 20px;
  cursor: pointer;
  transition: 850ms;
}

.menu__item {
  text-decoration: none;
  color: black;
  padding: 6px 0px;
  text-align: center;
  font-weight: bold;
  border-radius: 30px;
  width: 160px;
  margin: 20px auto;
  display: flex;
  justify-content: flex-start;
  align-items: baseline;
  transition: 850ms;
}
.menu__item:hover {
  background-color: #393e46;
  transition: 850ms;
  color: white;
}
.cancel__btn :hover {
  background-color: red;
  transition: 850ms;
}

.menu__icon {
  margin: 0 20px;
}

.nav_profile_image {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background-size: cover;
  margin-left: 30px;
  /* align-self: center; */
  border: none;
}

.nav_username {
  align-self: center;
  margin: 10px 0;
  color: #fff;
}

.nav_profile {
  background-color: #393e46;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.nav_menu_item_active {
  color: white;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #393e46;
  font-family: "Josefin Sans", sans-serif;
}

